import { createSlice } from '@reduxjs/toolkit';
import {
  register,
  logout,
  login,
  refreshUser,
  fetchUserAccount,
} from './operations';

const initialState = {
  user: {
    name: null,
    email: null,
    birthday: null,
    phone: null,
    skype: null,
    imageURL: null,
  },
  token: null,
  isLoggedIn: false,
  isRefreshing: false,
};

